<template>
    <section style="height: 100%; user-select:none;">
      <el-menu router :default-active="active" :collapse="isCollapse" active-text-color="#4B150C"
      @select="handleselect" class="menu-left" >
        <el-menu-item index="/register" v-if="userMap.includes('1001')">
          <i class="el-icon-menu"></i>
          <span slot="title">门诊挂号</span>
        </el-menu-item>
        <el-menu-item index="/clinical" v-if="userMap.includes('2001')">
          <i class="el-icon-zhzy-zhiliao"></i>
          <span slot="title">临症系统</span>
        </el-menu-item>
        <el-menu-item index="/acupuncture" v-if="userMap.includes('3001')">
          <i class="el-icon-zhzy-ziyuanx"></i>
          <span slot="title">针灸系统</span>
        </el-menu-item>
        <el-menu-item index="/anorectal" v-if="userMap.includes('4001')">
          <i class="el-icon-zhzy-gangchangke"></i>
          <span slot="title">肛肠系统</span>
        </el-menu-item>
        <el-menu-item index="/finance" v-if="userMap.includes('9001')">
          <i class="el-icon-zhzy-caiwu"></i>
          <span slot="title">财务系统</span>
        </el-menu-item>
        <el-menu-item index="/drug" v-if="userMap.includes('8001')">
          <i class="el-icon-zhzy-yaowu"></i>
          <span slot="title">药房系统</span>
        </el-menu-item>
        <el-menu-item index="/database" v-if="userMap.includes('5001')">
          <i class="el-icon-setting"></i>
          <span slot="title">系统设置</span>
        </el-menu-item>
        <!-- <el-submenu index="2">
          <template slot="title"><i class="el-icon-setting"></i><span>系统设置</span></template>
          <el-menu-item index="/database">
            <span slot="title">基础数据</span>
          </el-menu-item>
          <el-menu-item index="/database/user">
            <span slot="title">用户管理</span>
          </el-menu-item>
        </el-submenu> -->
        <el-menu-item index="/auth" v-if="userMap.includes('6001')">
          <i class="el-icon-zhzy-quanxian"></i>
          <span slot="title">权限系统</span>
        </el-menu-item>
        <el-submenu index="3" v-if="userMap.includes('7001')">
          <template slot="title"><i class="el-icon-zhzy-baobiao"></i><span>报表系统</span></template>
          <el-menu-item index="/reports/jiuzhen">
            <span slot="title">医生就诊记录</span>
          </el-menu-item>
          <el-menu-item index="/reports/ysgz">
            <span slot="title">医生工作量</span>
          </el-menu-item>
          <el-menu-item index="/reports/ksgz">
            <span slot="title">科室工作量</span>
          </el-menu-item>
        </el-submenu>
        <i class="el-icon-zhzy-outdent menu-button" v-if="!isCollapse" style="color:#fff" @click="isCollapse=true"></i>
        <i class="el-icon-zhzy-indent menu-button" v-if="isCollapse" style="color:#fff" @click="isCollapse=false"></i>
      </el-menu>
    </section>
</template>
<script>
import {mapMutations,mapState} from 'vuex'
export default {
  data(){
    return{
      isCollapse: false,
      active: '/register', // 默认激活样式
    }
  },
  computed:{
    ...mapState('user',{
      userMap:state=>state.userMap
    })
  },
  methods:{
    ...mapMutations('patient',['hiddenPatientInfo']), 
    handleselect(index, path) {
      this.hiddenPatientInfo() // 隐藏顶部患者信息
    },
  },
  created(){ // 刷新激活路由
      if(!this.$route.path.includes('reports')){
         this.active = '/' + this.$route.path.split('/')[1]
      }else{
        this.active = this.$route.path
      }     
  }
}
</script>
<style scoped>
.menu-left{
  height: 100%;
  min-height: calc(100vh - 60px);
}
.menu-left:not(.el-menu--collapse) {
  width: 206px;
}
/*

.el-menu .el-menu-item,.el-submenu__title span{
  font-size: 16px;
  color: #fff;
  letter-spacing: 2px;
}
.el-menu .el-menu-item i,.el-submenu__title i{
  color: #fff;
} */
/* .el-menu-item i{
  color: #fff;
} */
.el-menu .is-active:not(.el-submenu){
  background-color: #F7F0EF;
}
.el-menu .is-opened .el-submenu__title i,.el-menu .is-opened .el-submenu__title span{
  color: #F7F0EF !important;
}
/* .el-menu .is-active{
  background-color: #F7F0EF;
} */
.menu-button{
  position: absolute;
  font-size: 22px;
  left: 20px;
  bottom: 20px;
  cursor: pointer;
}
</style>





