<template>
    <el-row type="flex">
      <el-col :xs="8" :sm="6" :md="5" :lg="4" :xl="3">
        <div class="header-logo">
          <img src="../../../static/img/logo.png" alt="">
        </div>
      </el-col>
      <el-col>
        <div class="header-bar-mid">
        科室: {{keshi}}
          <span v-if="patientIsSave">
            <span>患者: {{patientName}}</span>
            <span>性别: {{patientSex}}</span>
            <span>年龄: {{patientAge}}</span>
            <span>孕否: {{patientIsYuFu}}</span>
            <span>体重: {{patientWeight}}</span>
            <span><el-button plain size="mini" icon="el-icon-edit" @click="handelOpenInfo">查看详情</el-button></span>
          </span>
        </div>
      </el-col>
      <el-col :xs="4" :sm="6" :md="5" :lg="4" :xl="3">
        <div class="header-bar-right">
          <span v-if="userName === 'ning'" @click="handleReload"> 测试刷新 </span>
          <img v-if="userSex =='1'" src="../..//assets/imgs/admin_head1.png" alt="">
          <img v-if="userSex =='0'" src="../..//assets/imgs/admin_head2.png" alt="">
          <el-dropdown style="margin-left: 16px" @command="handleCommand">
            <span class="el-dropdown-link" style="cursor: pointer;">
              {{userName}}<i class="el-icon-arrow-down el-icon--right"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item command="info">个人信息</el-dropdown-item>
              <el-dropdown-item command="passward">修改密码</el-dropdown-item>
              <el-dropdown-item command="logout">退出</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
        </el-col>
          <el-dialog
            :visible.sync="infoVisible"
            title="患者信息"
            align="center"
            width="70%">
            <patient-info style="padding: 0 12px;" ref="child" @childClose="handelCloseDialogInfo" :uid="patientRegistrationNum"></patient-info>
          </el-dialog>
          <el-dialog @close="dialogClosePwd"
            :visible.sync="dialogPassword"
            width="50%">
            <el-row>
              <el-col :span="23">
                <el-form :rules="rules" ref="formPwd" :model="passwordData" label-width="120px">
                    <el-form-item prop="oldpwd" label="原密码">
                      <el-input @keydown.enter.native="funChangePassword"
                      v-model="passwordData.oldpwd" placeholder="请输入原密码" type="password" ></el-input>
                    </el-form-item>
                    <el-form-item prop="password1" label="新密码">
                      <el-input @keydown.enter.native="funChangePassword"
                      v-model="passwordData.password1" placeholder="请输入新密码" type="password" ></el-input>
                    </el-form-item>
                    <el-form-item prop="password2" label="重复密码">
                      <el-input @keydown.enter.native="funChangePassword"
                      v-model="passwordData.password2" placeholder="请输入重复密码" type="password" ></el-input>
                    </el-form-item>
                </el-form>
              </el-col>
              <el-col :span="24" class="toolbar" align=center>
                <el-button @click="dialogPassword = false">取 消</el-button>
                <el-button type="primary" @click="funChangePassword">确 定</el-button>
              </el-col>
            </el-row>
          </el-dialog>
          <el-dialog
            title="个人信息" center @close="dialogCloseUser"
            :visible.sync="dialogUser"
            width="50%">
            <el-row>
              <el-col :span="23">
                <el-form :rules="rules" ref="formUser" :model="userData" label-width="120px">
                    <el-form-item label="姓名:" prop="username">
                      <el-input v-model="userData.username" placeholder="请输入原密码"></el-input>
                    </el-form-item>
                    <el-form-item label="性别:" prop="sex">
                      <el-select
                          v-model="userData.sex" placeholder="请选择性别" style="width:100%">
                            <el-option
                              v-for="item in selectSex"
                              :key="item.value"
                              :label="item.lable"
                              :value="item.value">
                            </el-option>
                      </el-select>
                    </el-form-item>
                    <el-form-item label="科室:">
                      <el-select multiple
                      v-model="deptSelectDisplay" disabled  style="width:100%">
                        <el-option
                          v-for="item in keshiData"
                          :key="item.id"
                          :label="item.deptName"
                          :value="item.deptCode">
                        </el-option>
                      </el-select>
                    </el-form-item>
                    <el-form-item label="联系方式:">
                      <el-input v-model="userData.mobile" placeholder="请输入联系方式"></el-input>
                    </el-form-item>
                    <el-form-item label="住址:">
                      <el-input v-model="userData.address" placeholder="请输入居住地址"></el-input>
                    </el-form-item>
                </el-form>
              </el-col>
              <el-col :span="24" class="toolbar" align=center>
                <el-button @click="dialogUser = false">取 消</el-button>
                <el-button type="primary" @click="funChangeUser">确 定</el-button>
              </el-col>
            </el-row>
          </el-dialog>
    </el-row>
</template>
<script>
import {
  logout,  // 登出
  getUserById, // 获取用户信息
  editPwdByAccount, // 重置密码
  updateUser, // 修改用户
} from '../../api'
import patient_info from '../../components/patient_info_window'
import {mapState,mapMutations,mapGetters} from 'vuex'
export default {
  components:{
    'patient-info': patient_info
  },
    data(){
      var validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请再次输入密码'));
        } else if (value !== this.passwordData.password1) {
          callback(new Error('两次输入密码不一致!'));
        } else {
          callback();
        }
      }
      return{
        photoImg: false, // 头像显示
        infoVisible: false, // 弹出框
        dialogPassword: false, // 修改密码
        dialogUser: false, // 修改用户信息
        cuid: '', // 子组件用于获取患者信息
        value:'',
        userName: '',
        userSex: '',
        mag: '',
        passwordData:{ // 修改改密码
          oldpwd: '',
          password1: '',
          password2: ''
        },
        userData:{ // 用户数据

        },
        selectSex: [
        {
          lable: '男',
          value: '1',
        },
        {
          lable: '女',
          value: '0',
        },
      ],
        deptSelectDisplay: [], // 用户科室显示值
        deptSelectData: '', // 用户科室值
        rules:{ // validator: validatePass2
          username: [{required: true, message: '用户名不能为空!', trigger: 'blur'}],
          oldpwd: [{required: true, message: '原密码不能为空!', trigger: 'blur'}],
          password2: [
            {required: true, message: '请再次输入密码!', trigger: 'blur'},
            {validator: validatePass2, trigger: 'blur'}
            ],
          password1: [
            {required: true, message: '密码不能为空!', trigger: 'blur'},
            {min: 4, max: 9, message: '密码长度需要4到9位!', trigger: 'blur'},]
      },
      //  userName: '未登录', // 用户名
      }
    },
    computed:{
      ...mapState('patient',{
        patientName: state => state.name,
        patientSex: state => state.sex,
        patientAge: state => state.age,
        patientIsYuFu: state => state.isYuFu,
        patientRegistrationNum: state => state.registrationNum,
        patientWeight: state => state.weight,
        patientIsSave: state => state.isSave,
      }),
      ...mapState('common',{
        keshiData:state=>state.keshi,
      }),
      keshi:function(){
        let ks='综合'
        let path = this.$route.path
        if(path.includes('register'))
          ks="护士站"
        if(path.includes('clinical'))
          ks="临症"
        if(path.includes('acupuncture'))
          ks="针灸"
        if(path.includes('anorectal'))
          ks="肛肠"
        if(path.includes('auth'))
          ks="权限管理"
        if(path.includes('finance'))
          ks="财务系统"
        if(path.includes('drug'))
          ks="药房系统"
        if(path.includes('register'))
          ks="护士站"
        return ks
      },
      patient:function(){ // 患者信息
        return this.$store.state.patient
      },
      registrationNum:function(){
        return this.$store.state.registrationNum
      }
    },
    methods:{
      ...mapMutations('user',['saveUserDataByInfo','removeUserData']),
      handelOpenInfo(){
        this.infoVisible = true
        this.$nextTick(()=>{
          this.$refs['child'].getData()
        })
      },
      handleReload () {
        window.location.reload()
      },
      handleCommand(command){ // 用户操作
        if(command == 'logout'){
          logout()// 后台退出接口
          this.removeUserData()
          this.$router.push('login')
        }
        if(command == 'passward'){
          this.passwordData.oldpwd = ''
          this.passwordData.password1 = ''
          this.passwordData.password2 = ''
          this.dialogPassword = true
        }
        if(command == 'info'){ // 个人信息 - 获取
          getUserById(sessionStorage.getItem('uuid')).then(res=>{
            if(res.data.code == '0'){
              let user = res.data.data.user
              this.userData = user
              sessionStorage.setItem('sex', user.sex)
              sessionStorage.setItem('username', user.username)
              this.deptSelectData = user.deptcode
              this.deptSelectDisplay = user.deptcode.toString().split(',')
              this.userName = user.username
              this.userSex = user.sex
            }
          })
          this.dialogUser = true
        }
      },
      dialogCloseUser(){ // 窗口关闭回调
        this.$refs.formUser.resetFields()
      },
      dialogClosePwd(){ // 窗口关闭回调
        this.$refs.formPwd.resetFields()
      },
      funChangeUser(){ // 修改用户
        if(!this.userData.uuid){
          return
        }
        this.$refs.formUser.validate((valid) => {
          if (valid) {
            let userData=this.userData
            userData.deptcode = this.deptSelectData
            updateUser(userData).then(res=>{
              if(res.data.code == '0'){
                this.$message.success('用户信息修改成功!')
                this.userName = userData.username
                this.userSex = userData.sex
                sessionStorage.setItem('sex', userData.sex)
                sessionStorage.setItem('username', userData.username)
              }else{
                this.$message.error('用户信息修改失败!')
              }
            })
          } else {
            return false
          }
        })
      },
      // 更改密码
      funChangePassword(){
        this.$refs.formPwd.validate((valid) => {
          if(valid){
              editPwdByAccount({
                oldpwd: this.passwordData.oldpwd,
                usercode: sessionStorage.getItem('usercode'),
                password: this.passwordData.password1,
              }).then(res=>{
                if(res.data.code=='0'){
                  this.$message.success('密码修改成功!')
                  this.dialogPassword = false
                }else{
                  this.$message.error(res.data.msg)
                }
              })
          }
        })

      },
      handelCloseDialogInfo(){
        this.infoVisible = false
      }
    },
    mounted:function(){
      this.userName = sessionStorage.getItem('username')
      this.userSex = sessionStorage.getItem('sex')
    }
}
</script>
<style scoped>
.header-logo{
  position: relative;
  height: 60px;
  /* border-right: 1px solid #aaa; */
  line-height: 60px;
  max-width: 206px;
}
.header-logo img{
  height: 39px;
  vertical-align: middle;
}
.header-bar-mid{
  padding-left: 20px;
  height: 60px;
  line-height: 80px;
  vertical-align: bottom;
}
.header-bar-mid span span{
  margin-left:24px;
}
.header-bar-right{
  padding-right: 14px;
  height: 60px;
  width: 100%;
  text-align: right;
  line-height: 60px;
}
.header-bar-right img{
  vertical-align: middle;
  max-width: 40px;
}

</style>




