// 引用示例
// this.$store.state.xxx.xxxx
import {
  getDrugWayTable, // 
  getUnitList, // 获取单位列表
  getDrugWay, // 服药方法
  getDosage, // 药剂类型
} from '../../api'
const state = {
  unitAllList: [], // 全部单位列表
  drugWayList: [], // 口服方法
  dosageList: [], // 药剂类型
  unitnameList: [], // 计量单位
  prescriptionunitnameList: [], // 处方单位
  singlemeasureunitnameList: [], // 单计量单位
};
const getters = {

};
const actions = {
};
const mutations = {
  getDatabaseCommon:(state)=>{
    getUnitList({pageSize: 99999}).then(res=>{
      if(res.data.code=='0'){
        state.unitAllList = res.data.data.content
      }
    })
    getDrugWay().then(res=>{
      if(res.data.code=='0'){
        state.drugWayList = res.data.data
      }
    })
    getDosage().then(res=>{
      if(res.data.code=='0'){
        state.dosageList = res.data.data
      }
    })
  }
};
export default {
  namespaced: true,  //目录文件夹命令空间
  state,
  mutations,
  actions,
  getters
};  


