// 引用示例
// this.$store.state.user.userId 
import {rescsbyrole} from '../../api'
const state = {
  uuid: "",
  username: '',
  usercode: '',
  deptcode: '',
  sex: '1',
  userMap: [], // 权限资源
  is_login: false
};
const mutations = {
  saveUserData(state, userInfo) {
    state.uuid = userInfo.uuid
    state.username = userInfo.username
    state.usercode = userInfo.usercode
    state.deptcode = userInfo.deptcode
    state.sex = userInfo.sex
    state.is_login = true
   sessionStorage.setItem("uuid", userInfo.uuid)
   sessionStorage.setItem("username", userInfo.username)
   sessionStorage.setItem("usercode", userInfo.usercode)
   sessionStorage.setItem("deptcode", userInfo.deptcode)
   sessionStorage.setItem("uuid", userInfo.uuid)
   sessionStorage.setItem("node", userInfo.node)
   sessionStorage.setItem("sex", userInfo.sex)
   sessionStorage.setItem("pId", userInfo.pId)
   sessionStorage.setItem("is_login", true)
  },
  // saveUserDataByInfo(state, userInfo){ // 个人信息更新
  //   state.username = userInfo.username
  //   state.sex = userInfo.sex
  //   sessionStorage.setItem("username", userInfo.username)
  //   sessionStorage.setItem("sex", userInfo.sex)
  // },

  removeUserData(state) {
    state.uuid = ""
    state.is_login = false
    state.userMap = []
    sessionStorage.removeItem('is_login')
    sessionStorage.removeItem('uaCodes')
    sessionStorage.removeItem("uuid")
  },
  getUserMap(state,callback){
    let codes = sessionStorage.getItem('uaCodes')
    if(!codes) return
    let arr = codes.split(',')
    arr.forEach(ele=>{
      rescsbyrole(ele).then(res=>{
        if(res.data.code == '0'){
          let tempArr = []
          res.data.data.forEach(item=>{
            if(!state.userMap.includes(item.code)){
              tempArr.push(item.code)
            }
          })
          state.userMap = [...state.userMap,...tempArr]
          if(callback){
            callback()
          }
        }
      })
    })
  }
};
const actions = {

};
const getters = {
  
};

export default {
  namespaced: true,  //目录文件夹命令空间
  state,
  mutations,
  actions,
  getters
};  


