// 公共方法
export default{
  install:function(Vue,opt){
    // 返回上一页
    Vue.prototype.goBack = function(){
      history.go(-1)
    },
    // 药性字符串格式化
    Vue.prototype.elFormatYaoXing = function(row, column, cellValue, index) {
     if(cellValue){
        return cellValue.replace(/[0-9;:]/g,' ')
     }else{
       return ''
     }
      
    },
    // 模板类型对照
    Vue.prototype.elFormatMuBanName = function(row, column, cellValue, index) {
      let name = ''
      switch (cellValue) {
          case 40:
        name = '主诉'
          break;
          case 20:
        name = '过敏史'
          break;
          case 30:
        name = '既往史'
          break;
          case 10:
        name = '现病史'
          break;
          case 50:
        name = '体格检查'
          break;
          case 70:
        name = '医嘱'
          break;
          case 60:
        name = '其他'
          break;
        default:
        name = ''
          break;
      }
      return name
    },
    Vue.prototype.elTableHeaderStyle = function(row, column, cellValue, index) {
      return{
        background: '#FAF7F6'
      }
    }
  }
}


