<template>
  <section>
    <el-row type="flex">
      <el-col :xs="8" :sm="6" :md="5" :lg="4" :xl="3">
        <div class="header-logo">
          <img src="../../../static/img/logo.png" alt="">
        </div>
      </el-col>
      <el-col class="database-menu">
        <div class="database-menu-inner">
          <span><i class="el-icon-caret-left" @click="handelScrollLeft"></i></span>
          <span class="database-menu-item">
          <span data-path="dict_xiyao" @click="handelLinkeToPath">西药字典</span>
          <span data-path="dict_zhongyao" @click="handelLinkeToPath">中药字典</span>
          <span data-path="dict_danwei" @click="handelLinkeToPath">单位设置</span>
          <span data-path="dict_muban" @click="handelLinkeToPath">模板设置</span>
          <span data-path="dict_xiangmu" @click="handelLinkeToPath">医疗项目</span>
          <span data-path="dict_huizong" @click="handelLinkeToPath">汇总项目</span>
          <span data-path="dict_mingxi" @click="handelLinkeToPath">明细项目</span>
          <span data-path="dict_kucun" @click="handelLinkeToPath">药物库存</span>
          </span>
          <span><i class="el-icon-caret-right" @click="handelScrollRight"></i></span>
        </div>
      </el-col>
      <!-- <el-col :xs="4" :sm="6" :md="5" :lg="4" :xl="3">
        <div class="header-bar-right"> dict_huizong
          <img src="../..//assets/imgs/admin_head2.png" alt="">
          <span>毛护士</span>
        </div>
      </el-col> -->
    </el-row>
  </section>
</template>
<script>
export default {
  data(){
    return{
      scrollLength: 0, // 滚动条
      scrollStep: 0, // 判断滚动是否触发
    }
  },
  methods:{
    handelLinkeToPath(e){ // 导航
      document.querySelector('.database-menu-item .active').className = ''
      e.target.className = 'active'
      this.$router.push(e.target.dataset.path)
    },
    handelScrollLeft(){ // 左滚动
      let bar = document.querySelector('.database-menu-item')
      let length = this.scrollLength
      length += 60
      bar.scrollLeft=length
      this.scrollStep = 60
    },
    handelScrollRight(){// 右滚动
      let bar = document.querySelector('.database-menu-item')
      let length = this.scrollLength
      length -= 60
      bar.scrollLeft=length
      this.scrollStep = -60
    }
  },
  mounted(){
    document.querySelector(".database-menu-item").addEventListener('scroll',()=>{
      this.scrollLength = this.scrollLength + this.scrollStep
    })
    let els = document.querySelectorAll('.database-menu-item span')
    els.forEach(element => {
      if(element.dataset.path == this.$route.path.split('/')[2]){
        element.className = 'active'
      }
    });
  }
}
</script>
<style scoped>
.header-logo{
  position: relative;
  height: 60px;
  /* border-right: 1px solid #aaa; */
  line-height: 60px;
  max-width: 206px;
}
.header-logo img{
  height: 39px;
  vertical-align: middle;
}
.database-menu{
  user-select:none;
}
.database-menu div{
  display: flex;
  /* align-items: center; */
  margin:  0 auto;
  overflow: hidden;
  padding-top: 16px;
  width: 86%;
  height: 52px;
}
.database-menu div span{
  display: inline-block;
  height: 26px;
  margin: 0 10px;
  height: 60px;
  font-size: 16px;
  color: #666;
}
.database-menu div .database-menu-item{
  overflow-x: auto;
  overflow-y: hidden;
  white-space:nowrap;
}
.database-menu div .database-menu-item::-webkit-scrollbar{
  display: none;
}
.database-menu div .database-menu-item span:hover,
.database-menu div span .active{
  cursor: pointer;
  color: #BF7045;
  padding-bottom: 3px;
  border-bottom: 2px solid #BF7045;
}
.database-menu i{
  color: #666;
  line-height: 24px;
  font-size: 24px;
}
.database-menu i:hover{
  cursor: pointer;
  color: #333;
}
</style>



