<template>
<el-container>
    <el-header class="main-header">
        <main-header v-if="!headerType"></main-header>
        <database-header v-if="headerType"></database-header>
    </el-header>
  <el-container class="heightFix">
    <el-aside style="width: auto;">
        <main-nav></main-nav>
    </el-aside>
    <el-main style="padding-top: 26px">
        <router-view class="main-box"></router-view>
    </el-main>
  </el-container>
</el-container>
</template>
<script>
import {
    getKeShi, // 科室列表
    getMecidalType, //就诊类型/类别
} from '../../api'
import header from './header'
import header_database from './header_database'
import nav from './nav'
export default {
    components:{
        'main-header': header,
        'database-header': header_database,
        'main-nav': nav,
    },
    computed:{
        headerType: function(){
            let patt = new RegExp("database")
            // true 表示在基础数据页面上
            return patt.test(this.$route.path)
        }
    },
    mounted(){
        getKeShi().then(res=>{
          this.$store.state.common.keshi = res.data.data
        })
        getMecidalType({checkFlag: 0}).then(res=>{
          this.$store.state.common.mecidalType = res.data.data
        })
        getMecidalType({checkFlag: 1}).then(res=>{
          this.$store.state.common.costType = res.data.data
        })
    }
}
</script>
<style>
/* 高度并列 */
.heightFix{
    display: flex;
    align-items: stretch;
}
</style>




