// 引用示例
// this.$store.state.xxx.xxxx
import {
  getKeShi, // 获取科室
  getDrugWayTable, // 获取用药方式
  getFrequencyTable, // 用药频率
} from '../../api'
const state = {
  keshi:[], // 科室序号列表
  mecidalType: [], // 就诊类型
  costType: [], // 费用类型
  registrationNum: '', // 挂号序号
  currentKeshi: '', // 当前科室
  drugWay: [], // 用药方式
  frequency: [], // 用药频率
};
const getters = {
  getDrugWay:(state)=>{
    if (state.drugWay.length>0) {
      return state.drugWay
    }else{
      getDrugWayTable().then(res=>{
        if(res.data.code=='0'){
          state.drugWay = res.data.data
        }else{
          state.drugWay = []
        }
      })
    }
  },
  // getFrequency:(state)=>{
  //   if (state.frequency.length>0) {
  //     return state.frequency
  //   }else{
  //     getFrequencyTable().then(res=>{
  //       if(res.data.code=='0'){
  //         state.frequency = res.data.data
  //       }else{
  //         console.log('getFrequencyTable Errow')
  //         state.frequency= []
  //       }
  //     })
  //   }
  // }
};
const actions = {
 getKeShiList() {
    getKeShi().then(res => {
      if (res.data.code === '1') {
        commit('setKeShiList', res.data.data)
      }
    })
  }
};
const mutations = {
  // 设置科室列表
  setKeShiList: (state, list) => {
    state.getKeShi = list
  },
  getFrequency:(state)=>{
    if (state.frequency.length>0) {
      return state.frequency
    }else{
      getFrequencyTable().then(res=>{
        if(res.data.code=='0'){
          state.frequency = res.data.data
        }else{
          state.frequency= []
        }
      })
    }
  }
};
export default {
  namespaced: true,  //目录文件夹命令空间
  state,
  mutations,
  actions,
  getters
};



