// 病例信息
const state = {
    chineseMedicineCode: "",
    chineseMedicineName: "",
    cureCode: "",
    cureName: "",
    dialecticalCode: "",
    dialecticalName: "",
    mainSymptomCode: "",
    mainSymptomName: "",
    westernMedicineCode: "",
    westernMedicineName: "",
};
const getters = {
};
const actions = {
};
const mutations = {
  saveTreatmentInfo(state,info){
    state.chineseMedicineCode= info.chineseMedicineCode || ""
    state.chineseMedicineName= info.chineseMedicineName || ""
    state.cureCode= info.cureCode || ""
    state.cureName= info.cureName || ""
    state.dialecticalCode= info.dialecticalCode || ""
    state.dialecticalName= info.dialecticalName || ""
    state.mainSymptomCode= info.mainSymptomCode || ""
    state.mainSymptomName= info.mainSymptomName || ""
    state.westernMedicineCode= info.westernMedicineCode || ""
    state.westernMedicineName= info.westernMedicineName || ""
  }
};
export default {
  namespaced: true,  //目录文件夹命令空间
  state,
  mutations,
  actions,
  getters
}; 


