import Vue from 'vue'
import { Message } from 'element-ui'
import Router from 'vue-router'
import NProgress from 'nprogress'
import store from '../store'
import routes from './routes'

Vue.use(Router)

const router = new Router({
  routes
})
router.beforeEach((to, from, next) => {
    NProgress.start()
    let getUserMap = store.state.user.userMap
    if(getUserMap.length == 0 && to.path != '/login' && sessionStorage.getItem('is_login')){ // 获取权限
      store.commit('user/getUserMap',function () {
        next()
      })
    }else{
       
        sessionStorage.removeItem('current_deptCode')
        if(to.path.includes('clinical')){
          sessionStorage.setItem('current_deptCode','10001')
        }
        if(to.path.includes('acupuncture')){
          sessionStorage.setItem('current_deptCode','10008')
        }
        if(to.path.includes('anorectal')){
          sessionStorage.setItem('current_deptCode','10004')
        }
      if(sessionStorage.getItem('is_login')){
        next()
      }else{
        if(to.path == '/login' || to.path == '/sign'){
          NProgress.done()
          next()
          return
        }else{
          NProgress.done()
          next({ path: '/login' })
        }
      }
    }
})
router.afterEach(transition => {
  NProgress.done()
})

export default router



