<template>
  <el-row>
    <el-form label-width="100px" :model="formData" :rules="rules">
      <el-col :offset="0" :span="11">
        <el-form-item label="挂号序号：">
          <el-input v-model="formData.registrationNum" readonly disabled></el-input>
        </el-form-item>
        <el-form-item label="就诊科室：" prop="deptCode">
          <el-select v-model="formData.deptCode" @change="selectDept" placeholder="请选择">
              <el-option
                v-for="item in optionsDeptName"
                :key="item.id"
                :label="item.deptName"
                :value="item.deptCode">
              </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="就诊时间：" prop="onSetDate">
            <el-date-picker
            v-model="formData.onSetDate"
            type="datetime"
            value-format="yyyy-MM-dd HH:mm:ss"
            placeholder="选择日期时间">
          </el-date-picker>
      </el-form-item>
      <el-form-item label="性别：">
          <el-select v-model="formData.emSex" placeholder="请选择">
            <el-option
              v-for="item in optionsSex"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
      </el-form-item>
      <el-form-item label="联系人：">
        <el-input v-model="formData.emComName"></el-input>
      </el-form-item>
      <el-form-item label="费用类别：" prop="emType">
          <el-select v-model="formData.emType" placeholder="请选择" size="medium">
            <el-option
              v-for="item in costType"
              :key="item.value"
              :label="item.mecidalTypeName"
              :value="item.mecidalTypeCode">
            </el-option>
          </el-select>
      </el-form-item>
      <el-form-item label="孕否：">
          <el-select v-model="formData.isYuFu" placeholder="请选择">
            <el-option
              v-for="item in optionsYuFu"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
      </el-form-item>
      <el-form-item label="体重：">
        <el-input v-model="formData.weight"></el-input>
        <span class="input-fix">kg</span>
      </el-form-item>
      <el-form-item label="添加人：">
        <el-input v-model="formData.maker"></el-input>
      </el-form-item>
      </el-col>
      <el-col :offset="1" :span="11">
      <el-form-item label="就诊号：" prop="mecidalNum">
        <el-input v-model="formData.mecidalNum"></el-input>
      </el-form-item>
      <el-form-item label="就诊医生：" prop="userName">
        <el-autocomplete v-model="formData.userName"
          :fetch-suggestions="queryUserList"
          @select="handleSelect"
          style="width: 100%"
          placeholder="请先选择就诊科室"
        ></el-autocomplete>
      </el-form-item>
      <el-form-item label="姓名：" prop="emName">
        <el-input v-model="formData.emName"></el-input>
      </el-form-item>
      <el-form-item label="年龄：">
        <el-input v-model="formData.emAge"></el-input>
      </el-form-item>
      <el-form-item label="联系电话：">
        <el-input v-model="formData.emMobile"></el-input>
      </el-form-item>
      <el-form-item label="就诊类别：" prop="mecidalType">
          <el-select v-model="formData.mecidalType" placeholder="请选择" size="medium">
            <el-option
              v-for="item in mecidalType"
              :key="item.value"
              :label="item.mecidalTypeName"
              :value="item.mecidalTypeCode">
            </el-option>
          </el-select>
      </el-form-item>
      <el-form-item label="住址：">
        <el-input v-model="formData.emAddress"></el-input>
      </el-form-item>
      <el-form-item label="身份证号：" prop="emNum">
        <el-input v-model="formData.emNum"></el-input>
      </el-form-item>
      <el-form-item label="添加时间：">
          <el-date-picker
          v-model="formData.makeDate"
          type="datetime"
          value-format="yyyy-MM-dd HH:mm:ss"
          placeholder="选择日期时间">
        </el-date-picker>
      </el-form-item>
      </el-col>
    </el-form>
    <el-col :span="24" align="center" class="toolbar">
      <el-button type="primary" style="margin-right: 90px" @click="update">保 存</el-button>
      <el-button type="primary" @click="handelClose">关 闭</el-button>
    </el-col>
  </el-row>
</template>
<script>
import { mapState,mapMutations} from 'vuex'
import {
  getSelectPatientInfoTable, // 获取患者详情
  updatePatientInfo, // 修改患者详情
  getGyUser, // 获取医生
  } from "../api"
export default {
 props:['uid'],
    computed:{
    ...mapState({
      optionsDeptName: state => state.common.keshi, // 科室信息
      mecidalType: state=> state.common.mecidalType, // 就诊类型
      costType: state=> state.common.costType,
    }),
  },
  data(){
    return{
      formData:{
        emType: '',
      }, // 表单数据
      // uid: function () {
      //   return sessionStorage.getItem('current_registrationNum')
      // },
      optionsYuFu:[ // 是否怀孕
        {
          value: 0,
          label: '否'
        },
        {
          value: 1,
          label: '是'
        }
      ],
       optionsSex:[{
          value: 1,
          label: '男'
       },
       {
          value: 0,
          label: '女'
       }],
      optionsMecidal: [], // 选项 就诊类别 后台
      rules:{ // 验证规则
        emName:[
          {required: true, message: '姓名不能为空', trigger: 'blur'}
        ],
      },
      rules:{ // 验证规则
        emName:[{required: true, message: '姓名不能为空', trigger: 'blur'}],
        mecidalNum:[{required: true, message: '就诊号不能为空', trigger: 'blur'}],
        deptCode:[{required: true, message: '就诊科室不能为空', trigger: 'change'}],
        userName:[{required: true, message: '就诊医生不能为空', trigger: 'change'}],
        mecidalType:[{required: true, message: '就诊类别不能为空', trigger: 'change'}],
        emNum: [{required: true, message: '身份证号不能为空', trigger: 'blur'}],
        emType: [{required: true, message: '费用类别不能为空', trigger: 'change'}],
        onSetDate: [{required: true, message: '就诊时间不能为空', trigger: 'change'}],
      },
    }
  },
  methods:{
    ...mapMutations('patient',['getPatientInfo']),
    handelClose(){ // 关闭窗口
      this.$emit('childClose')
    },
    update(){
      updatePatientInfo(this.formData).then(res=>{
        if(res.data.code=='1'){
          this.$message.success('患者信息修改成功')
          // 更新store 用户信息
          this.getPatientInfo({id:this.formData.registrationNum})
          this.getData()
          this.handelClose()
        }else{
          this.$message.error('患者信息修改失败')
        }
      })
    },
      queryUserList(queryString,cb){ // 查询医生列表
      if(this.formData.deptCode == ''){
          this.$message.error('请先选择就诊科室')
          cb([])
      }else{
        getGyUser({deptCode: this.formData.deptCode}).then(res=>{
          let arr=[]
          if(res.data.code == '1'){
            this.$message.error('科室没有医生')
            cb([])
          }else{
            res.data.data.forEach(element=>{
              arr.push({value: element.userName,id: element.userCode})
            })
            cb(arr)
          }
        })
      }
    },
    handleSelect(item){ // 选择医生
      this.formData.userCode = item.id
    },
    selectDept(item){
      let DeptName = ''
      this.optionsDeptName.forEach(element => {
        if(element.id==item){
          DeptName = element.deptName
        }
      })
      this.formData.deptName = DeptName
    },
    getData(){
      getSelectPatientInfoTable({registrationNum: this.uid}).then(res=>{
        this.formData = res.data.data
        this.formData.emType = this.formData.emType.toString()
        this.formData = {...this.formData}
      })
    }
  },
  mounted(){
    // 改有父组件触发getData
    // getSelectPatientInfoTable({registrationNum: this.uid}).then(res=>{
    //   this.formData = res.data.data
    //   this.formData.emType = this.formData.emType.toString()
    // })
  }
}
</script>

<style scoped>
.huanzhe{
  padding-top: 24px;
}
.el-select,.el-date-editor{
  width: 100%;
}
input{
  min-width: 480px !important;
}
.huanzhe-btn{
  margin-top: 24px;
}
.huanzhe-btn button{
  display: block;
  margin: 0 auto;
}
</style>


