// 引用示例
// this.$store.state.xxx.xxxx
// 患者状态 patient
import {getSelectPatientInfoTable} from '../../api'
const state = {
  empBasicMainID: '', // 病单号
  name:'', // 姓名
  age: '',
  sex: '',
  isYuFu: '',
  registrationNum: '', // 挂号序号
  mecidalNum: '', // 就诊号
  deptCode: '',
  weight: '0',
  isSave: false
};
const mutations = {
  savePatientInfo(state, info){ // components/patient_list 调用
    state.name = info.name
    state.age = info.age
    state.registrationNum = info.registrationNum
    state.weight = info.weight || 0
    state.isYuFu = info.isYuFu=='1'? '是': '否'
    state.sex = info.sex=='1'? '男': '女'
    state.isSave = true
    state.deptCode = info.deptCode || 20 // 此默认值 临时使用
    state.mecidalNum = info.mecidalNum
    state.empBasicMainID = info.empBasicMainID
  },
  getPatientInfo(state,data,callback){
    state.empBasicMainID = ''
    state.registrationNum = data.id
    getSelectPatientInfoTable({registrationNum: data.id}).then(res=>{
      if(res.data.code == '0'){
        let info = res.data.data
        state.name = info.emName
        state.age = info.emAge
        state.registrationNum = info.registrationNum
        state.weight = info.weight || 0
        state.isYuFu = info.isYuFu=='1'? '是': '否'
        state.sex = info.emSex=='1'? '男' : '女'
        state.deptCode = info.deptCode || 20 // 此默认值 临时使用
        state.mecidalNum = info.mecidalNum
        state.empBasicMainID = info.empBasicMainID
        sessionStorage.setItem('eid',info.empBasicMainID)
        if(data.cb){
          data.cb()
        }
      }
    })
  },
  showpatientInfo(state){
    state.isSave = true
  },
  hiddenPatientInfo(state){
    state.isSave = false
  }
};
const actions = {

};
const getters = {

};

export default {
  namespaced: true,  //目录文件夹命令空间
  state,
  mutations,
  actions,
  getters
};  


