import Vue from 'vue';
import Vuex from 'vuex';
import common from './common';
import user from './user';
import patient from './patient';
import caseNote from './caseNote';
import database from './database';

Vue.use(Vuex);

export default new Vuex.Store({
  modules: {
    common,
    user,
    patient,
    caseNote,
    database,
  },
});


